/* shutter overlay */
.shutter-overlay {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  background-color: black;
  visibility: hidden;
  opacity: 0;
  z-index: 10;
  transition-property: opacity, visibility;
  transition-duration: 0.2s;
  transition-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
}

