.catalog-nav__list > li > ul li {
  margin-top: 1rem;
}

@media (max-width: 639px) {
  .catalog-nav__list > li > ul a {
    color: #847a74;
  }
}

.catalog-shutter-overlay {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 9900;
  opacity: 0;
  pointer-events: none;
  user-select: none;
  background-color: rgba(0, 0, 0, 0.5);
  will-change: opacity;
  transition: opacity 0.2s;
}

.catalog-shutter-overlay.catalog-is-open {
  opacity: 1;
}

/* catalog shutter end */
/* catalogue */
.content .catalogue {
  margin: 0 -6px;
}

.content .catalogue * {
  margin: 0;
}

.content .catalogue li {
  display: inline-block;
  vertical-align: top;
  width: 225px;
  margin: 0 3px 30px 6px;
  position: relative;
  padding: 0;
  background: none;
}

.catalogue li::before, .article-content .catalogue li::before, .typography .catalogue li::before {
  content: none;
}

.catalogue li.active {
  background: url("../i/catalogue-item-active.png") repeat-y -225px 0;
  margin: 0 3px 21px 6px;
  z-index: 2;
}

.catalogue .item {
  padding: 0 10px;
}

.catalogue li.active .item {
  background: url("../i/catalogue-item-active.png") no-repeat 0 0;
  padding-top: 1px;
}

.catalogue .image {
  width: 203px;
  height: 179px;
  border: 1px solid #f1ead6;
  text-align: center;
  line-height: 179px;
